import { Component, OnInit } from '@angular/core';

import { BridgeService } from '../../bridge.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  private render_data = null;
  private plc_data = null;
  private waiting = false;

  constructor(private bridgeService: BridgeService) { }

  ngOnInit() {
    
  }

  retrieve(epicenter: String){
    this.waiting = true;
    this.bridgeService.updateData(epicenter).subscribe((data)=>{
      console.log(data);
    });
    setTimeout(()=>{
      this.waiting=false;
      this.bridgeService.retrieveStations(epicenter).subscribe((data)=>{
        this.render_data = data;
      });
      this.bridgeService.retrievePlc(epicenter).subscribe((data)=>{
        this.plc_data = data;
      });
    }, 7000);
  }

  openMap(result){
    const url = "https://maps.google.com/?q="+result.hospital_lat+","+result.hospital_lon+"&ll="+result.hospital_lat+","+result.hospital_lon+"&z=15";
    window.open(url, '_blank');
  }

  openMapPlc(result){
    console.log(result);
    const url = "https://maps.google.com/?q="+result.police_lat+","+result.police_lon+"&ll="+result.police_lat+","+result.police_lon+"&z=15";
    window.open(url, '_blank');
  }

}
